ansform: translate(32.4904vw, -10px) scale(0.9787);\n  animation: fall-200 24s -29s linear infinite;\n}\n@keyframes fall-200 {\n  68.784% {\n    transform: translate(28.2499vw, 68.784vh) scale(0.9787);\n  }\n  to {\n    transform: translate(30.37015vw, 100vh) scale(0.9787);\n  }\n}","@charset \"utf-8\";\n/* byui scss全局变量开始 */\n/*浅色风格开始*/\n$base-menu-background: #e7eaed;\n$base-menu-active-background: rgba(#1890ff, 0.5);\n$base-menu-text: #001529;\n$base-menu-text-active: #001529;\n$base-title: #001529;\n/*浅色风格结束*/\n/*黑色风格开始*/\n$base-menu-background: #001529;\n$base-menu-active-background: #1890ff;\n$base-menu-text: #ffffff;\n$base-menu-text-active: #ffffff;\n$base-title: #ffffff;\n/*黑色风格结束*/\n/*蓝色风格开始*/\n$base-menu-background: #2a58ad;\n$base-menu-active-background: #22468a;\n$base-menu-text: #ffffff;\n$base-menu-text-active: #ffffff;\n$base-title: #ffffff;\n/*蓝色风格结束*/\n$base-font-size-small: 12px;\n$base-font-size-default: 14px;\n$base-font-size-big: 16px;\n$base-font-size-bigger: 18px;\n$base-font-size-max: 22px;\n$base-color-header: $base-menu-background;\n$base-color-blue: #1890ff;\n$base-color-white: #ffffff;\n$base-color-black: #000000;\n$base-color-yellow: #ffba00;\n$base-color-red: #ff4d4f;\n$base-color-gray: #97a8be;\n$base-main-width: 1140px;\n$base-border-radius: 2px;\n$base-border-color: #ebeef5;\n$base-form-width: 600px;\n$base-input-height: 32px;\n$base-pagination-height: 28px;\n$base-dialog-title-height: 40px;\n$base-padding: 15px;\n$base-box-shadow: 0 1px 4px rgba(0, 21, 41, 0.08);\n$base-font-color: #606266;\n$base-left-menu-width: 225px;\n$base-right-content-width: calc(100vw - 225px);\n$base-left-menu-width-min: 65px;\n$base-right-content-width-min: calc(100vw - 65px);\n/* byui scss全局变量结束 */\n:export {\n  menuText: $base-menu-text;\n  menuTextAcive: $base-menu-text-active;\n  menuBackground: $base-menu-background;\n  menuActiveBackground: $base-menu-active-background;\n  tagViewsActiveBackground: $base-color-blue;\n}\n","@import \"~@/styles/variables.scss\";\r\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n.content {\n  background: radial-gradient(ellipse at bottom, #1b2735 0%, #090a0f 100%);\n  overflow: hidden;\n  filter: drop-shadow(0 0 10px white);\n  position: relative;\n  width: 100%;\n  height: 100%;\n}\n\n@function random_range($min, $max) {\n  $rand: random();\n  $random_range: $min + floor($rand * (($max - $min) + 1));\n  @return $random_range;\n}\n\n.snow {\n  $total: 200;\n  position: absolute;\n  width: 10px;\n  height: 10px;\n  background: white;\n  border-radius: 50%;\n\n  @for $i from 1 through $total {\n    $random-x: random(1000000) * 0.0001vw;\n    $random-offset: random_range(-100000, 100000) * 0.0001vw;\n    $random-x-end: $random-x + $random-offset;\n    $random-x-end-yoyo: $random-x + ($random-offset / 2);\n    $random-yoyo-time: random_range(30000, 80000) / 100000;\n    $random-yoyo-y: $random-yoyo-time * 100vh;\n    $random-scale: random(10000) * 0.0001;\n    $fall-duration: random_range(10, 30) * 1s;\n    $fall-delay: random(30) * -1s;\n\n    &:nth-child(#{$i}) {\n      opacity: random(10000) * 0.0001;\n      transform: translate($random-x, -10px) scale($random-scale);\n      animation: fall-#{$i} $fall-duration $fall-delay linear infinite;\n    }\n\n    @keyframes fall-#{$i} {\n      #{percentage($random-yoyo-time)} {\n        transform: translate($random-x-end, $random-yoyo-y) scale($random-scale);\n      }\n\n      to {\n        transform: translate($random-x-end-yoyo, 100vh) scale($random-scale);\n      }\n    }\n  }\n}\n"]}]);
